import * as React from 'react';
import styled from 'styled-components';
import classnames from 'classnames';


const ButtonBox = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 7px;
  height: 26px;
  width: fit-content;
  border-radius: 4px;
  cursor: pointer;
  background: none;
  outline: none;
  border: 0;
  transform-origin: 50% 50%;
  background-color: #FFF;
  color: #FFF;
  background-color: rgb(0, 137, 255);

  &:not(.finished):hover {
    background-color: rgb(93, 159, 255);
  }
  &:not(.finished):active {
    background-color: rgb(57, 95, 212);
  }

  &.finished {
    color: rgb(186, 187, 189);
    background-color: #FFF;
    box-shadow: 0 0 0 1px rgb(223, 225, 227);
  }
  
`;


export interface FinishButtonProps extends React.ButtonHTMLAttributes<HTMLDivElement> {
  finished: boolean;
  text?: string;
}

const FinishButton: React.FC<FinishButtonProps> = (props) => {
  const { finished, text, className, ...rest } = props;

  const finishedRef = React.useRef(finished);

  const classNames = classnames(className, {
    finished,
    'finish-in': !finishedRef.current && finished,
  });

  finishedRef.current = finished;

  return (
    <ButtonBox {...rest} className={classNames}>
      {text}
    </ButtonBox>
  );
};

FinishButton.displayName = 'FinishButton';

export default FinishButton;
